.stats {
  background-color: var(--dark-desaturated-blue-card-bg);
  border-radius: 8px;
  overflow: hidden;
}
@media (min-width: 71.875em) {
  .stats {
    width: 1110px;
    display: grid;
    grid-template-columns: repeat(2, auto);
    justify-content: space-between;
  }
}
.stats__image {
  display: block;
  position: relative;
  display: flex;
  width: 100%;
}
@media (min-width: 71.875em) {
  .stats__image {
    width: auto;
    height: 447px;
    order: 1;
  }
}
.stats__image::after {
  content: "";
  background-color: var(--soft-violet-accent);
  width: 100%;
  height: 100%;
  position: absolute;
  opacity: 0.7;
}
@media (min-width: 71.875em) {
  .stats__image::after {
    z-index: 1;
  }
}
.stats .container {
  padding: 45px 35px 40px;
}
@media (min-width: 71.875em) {
  .stats .container {
    padding: 79px 121px 0 73px;
    width: 570px;
    order: 0;
  }
}
.stats__headline {
  color: var(--white-main-heading-stats);
  font-size: 1.6875rem;
  font-family: var(--font-lexend);
  text-align: center;
}
.stats__headline .insights {
  color: var(--soft-violet-accent);
}
@media (min-width: 40em) {
  .stats__headline {
    font-size: 2.125rem;
  }
  .stats__headline br {
    display: none;
  }
}
.stats__persuade {
  color: var(--slightly-transparent-white-stat-headings);
  margin-top: 0;
  margin-bottom: 1.875rem;
  text-align: center;
  line-height: 1.8;
  font-size: 15px;
}
@media (min-width: 71.875em) {
  .stats__persuade {
    text-align: left;
  }
  .stats__persuade br {
    display: none;
  }
}
.stats__numbers {
  margin-top: 0;
  text-align: center;
  font-size: 1.5rem;
  font-weight: 700;
}
@media (min-width: 71.875em) {
  .stats__numbers {
    display: flex;
    justify-content: space-between;
  }
}
.stats__numbers li {
  margin-bottom: 1rem;
  color: var(--white-main-heading-stats);
}
.stats__numbers li:last-child {
  text-transform: uppercase;
}
.stats__numbers span {
  color: var(--slightly-transparent-white-stat-headings);
  display: block;
  margin-top: 0.25rem;
  font-size: 0.75rem;
  letter-spacing: 0.1rem;
  text-transform: uppercase;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

body {
  background-color: var(--background);
  color: var(--text-color);
  font-family: var(--font-inter);
  font-size: 15px;
  margin: 0;
  min-height: calc(100vh - 0.1px);
  padding: 0;
}

.visually-hidden {
  position: absolute;
  left: -10000px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
}

.attribution {
  margin-top: 0.5rem;
  font-size: 11px;
  text-align: center;
}

.attribution a {
  color: hsl(277, 64%, 61%);
  font-weight: 700;
}

:root {
  --very-dark-blue-main-bg: hsl(233, 47%, 7%);
  --dark-desaturated-blue-card-bg: hsl(244, 38%, 16%);
  --soft-violet-accent: hsl(277, 64%, 61%);
  --white-main-heading-stats: hsl(0, 0%, 100%);
  --slightly-transparent-white-main-paragraph: hsla(0, 0%, 100%, 0.75);
  --slightly-transparent-white-stat-headings: hsla(0, 0%, 100%, 0.6);
  --background: var(--very-dark-blue-main-bg);
  --text-color: var(--slightly-transparent-white-main-paragraph);
}

:root {
  --font-inter: "Inter", sans-serif;
  --font-lexend: "Lexend Deca", sans-serif;
}

body {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

main {
  width: 20.4375rem;
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

ul {
  list-style-type: none;
  padding-left: 0;
}

a,
a:visited,
a:active {
  text-decoration: none;
}